#container-circles{position:absolute;left:50%;bottom:90px}
#outer-circle{left:-37px;height:75px;width:75px;background-color:hsla(0,0%,100%,.4);z-index:1}#inner-circle,#outer-circle{position:absolute;border-radius:50%}#inner-circle{left:50%;top:38px;height:44px;width:44px;background:#fff;margin:-22px 0 0 -22px;z-index:2} 
#inner-circle.is-clicked{height:38px;width:38px;margin:-19px 0 0 -19px}
#white-flash.normal{position:absolute;height:100%;width:100%;opacity:1;-webkit-transition:opacity .9s ease-out;-o-transition:opacity .9s ease-out;transition:opacity .9s ease-out}#white-flash.do-transition{opacity:0;background:#fff}
#display-error{color:#000;background-color:#fff}
.react-html5-camera-photo{position:relative;text-align:center}
.react-html5-camera-photo>img,.react-html5-camera-photo>video{width:768px}.react-html5-camera-photo>.display-error{width:768px;margin:0 auto}@media(max-width:768px){.react-html5-camera-photo>.display-error,.react-html5-camera-photo>img,.react-html5-camera-photo>video{width:100%}}.react-html5-camera-photo-fullscreen>img,.react-html5-camera-photo-fullscreen>video{width:100vw;height:50vh}.react-html5-camera-photo-fullscreen>.display-error{width:100vw;height:50vh}
